body {
  overflow-x: hidden; /* Empche les dbordements horizontaux */
}

.clases-container {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Deux colonnes par dfaut */
  grid-gap: 30px; /* Espacement entre les colonnes et les ranges */
}
.clases-container {
  max-width: 100%;
  box-sizing: border-box; /* Inclure padding et border dans les dimensions */
  overflow: hidden; /* Empche les dbordements */
}

.clases-title {
  grid-column: span 2; /* Le titre prend toute la largeur des deux colonnes */
  text-align: center;
  font-size: 2rem;
  color: white;
  margin-bottom: 20px;
}

.clases-section {
  opacity: 0;
  transition: opacity 2s ease, transform 2s ease; /* Transition fluide pour l'opacit et la transformation */
  margin-bottom: 30px;
  transform: translateX(-100vw); /* Par dfaut, dplacer les lments en dehors de l'cran  gauche */
}

/* Animation pour la colonne gauche (vient de la gauche) */
.clases-section.left {
  transform: translateX(-100vw); /* Hors de l'cran  gauche */
  color: white;
}

.clases-section.left.visible {
  opacity: 1;
  transform: translateX(0); /* Revient  sa position initiale */
}

/* Animation pour la colonne droite (vient de la droite) */
.clases-section.right {
  transform: translateX(100vw); /* Hors de l'cran  droite */
  color: white;
}

.clases-section.right.visible {
  opacity: 1;
  transform: translateX(0); /* Revient  sa position initiale */
}

/* Pour les petits crans, revenir  une seule colonne */
@media (max-width: 768px) {
  .clases-container {
    grid-template-columns: 1fr; 
    padding: 0;
    grid-gap: 0;
  }

  .clases-title {
    grid-column: span 1; /* Le titre prend la largeur d'une seule colonne */
    font-size: 1.5rem; /* Rduire lgrement la taille du titre */
    margin-bottom: 15px;
  }

  /* Animation fluide en venant du bas pour les petits crans */
  .clases-section.left,
  .clases-section.right {
    transform: translateY(100px); /* Hors de l'cran en bas */
  }

  .clases-section.visible {
    opacity: 1;
    transform: translateY(0); /* Revient  sa position initiale */
  }

  .clases-section ul {
    text-align: left;
  }

  .clases-section li {
    text-align: left;
    font-size: 14px; /* Rduction de la taille de la police si ncessaire */
  }

  
  .clases-images {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    opacity: 1; 
    transform: translateX(0); 
  }

  .clases-image {
    width: 50px;
    height: 50px;
    flex-shrink: 0; 
  }

}


.clases-images {
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  transform: translateX(100vw);
  transition: opacity 2s ease, transform 2s ease;
  margin-top: 20px;
}

.clases-images.visible {
  opacity: 1;
  transform: translateX(0);
}

.clases-image {
  width: 100px;
  height: 100px;
  flex-shrink: 0;
}