.explanation-agua {
    position: relative;
    border-radius: 8px;
    padding: 20px;
    width: 100%; /* Par dfaut, occupe toute la largeur */
    max-width: 610px; /* Largeur maximale sur les grands crans */
    margin: 0 auto; /* Centr */
    overflow: hidden; 
}

.explanation-agua::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../../../assets/images/elements/Agua1.png");
    opacity: 0.6; /* Transparence de l'image de fond */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1; /* Met l'image derrire le contenu */
    transition: opacity 0.3s ease;
}

.explanation-agua h4 {
    color: #c3a155;
    margin-top: -10px;
    font-size: 1.5rem;
    text-align: center;
    text-transform: uppercase;
    position: relative; /* Assure que le texte est au-dessus de l'image */
    z-index: 1;
}

.explanation-agua p {
    color: #ffffff;
    font-size: 1rem;
    line-height: 1.6;
    position: relative; /* Assure que le texte est au-dessus de l'image */
    z-index: 1;
}

/* ---- MEDIA QUERIES ---- */

/* Pour les petits crans */
@media (max-width: 768px) {
    .explanation-agua {
        padding: 15px; /* Rduit l'espace intrieur */
        max-width: 90%; /* Limite la largeur  90% de l'cran */
        border-radius: 6px; /* Coins lgrement arrondis */
    }

    .explanation-agua h4 {
        font-size: 1.2rem; /* Rduit la taille de la police du titre */
    }

    .explanation-agua p {
        font-size: 0.9rem; /* Rduit la taille des paragraphes */
    }
}

/* Pour les trs petits crans (mobiles en portrait) */
@media (max-width: 480px) {
    .explanation-agua {
        padding: 10px; /* Rduit encore l'espace intrieur */
    }

    .explanation-agua h4 {
        font-size: 1rem; /* Encore plus petit pour le titre */
    }

    .explanation-agua p {
        font-size: 0.8rem; /* Texte plus petit pour les paragraphes */
        line-height: 1.4; /* Rduit l'espacement des lignes */
    }
}

/* Pour les grands crans */
@media (min-width: 1200px) {
    .explanation-agua {
        max-width: 700px; /* Augmente la largeur pour les crans larges */
        padding: 25px; /* Augmente l'espace intrieur */
    }

    .explanation-agua h4 {
        font-size: 1.8rem; /* Agrandit le titre */
    }

    .explanation-agua p {
        font-size: 1.1rem; /* Texte lgrement plus grand */
    }
}
