/* Style de base */
.explanation-eter {
    background-image: url("../../../assets/images/elements/eter1.png"); /* Assurez-vous que le chemin est correct */
    background-size: cover; /* Ajuste la taille de l'image pour couvrir tout l'lment */
    background-position: center; /* Centre l'image de fond */
    background-repeat: no-repeat; /* Empche la rptition de l'image */
    border-radius: 8px;
    padding: 20px;
    width: 100%; /* Par dfaut, occupe toute la largeur */
    max-width: 610px; /* Largeur maximale sur les grands crans */
    margin: 0 auto; /* Centr */
    position: relative; /* Assure que les lments positionns absolument se trouvent  l'intrieur de cet lment */
    transition: transform 0.3s ease, opacity 0.3s ease; 
}

.explanation-eter h4 {
    color: #754f76;
    font-size: 1.5rem;
    margin-top: -10px;
    text-align: center;
    text-transform: uppercase;
}

.explanation-eter p {
    color: #ffffff;
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 10px;
}

.explanation-eter p strong {
    color: #ffffff;
    font-weight: bold;
}

/* ---- MEDIA QUERIES ---- */

/* Pour les petits crans (768px) */
@media (max-width: 768px) {
    .explanation-eter {
        padding: 15px; /* Rduit l'espace intrieur */
        max-width: 90%; /* Limite la largeur  90% de l'cran */
        border-radius: 6px; /* Coins lgrement arrondis */
    }

    .explanation-eter h4 {
        font-size: 1.2rem; /* Rduit la taille de la police du titre */
    }

    .explanation-eter p {
        font-size: 0.9rem; /* Rduit la taille des paragraphes */
    }
}

/* Pour les trs petits crans (480px) */
@media (max-width: 480px) {
    .explanation-eter {
        padding: 10px; /* Rduit encore l'espace intrieur */
    }

    .explanation-eter h4 {
        font-size: 1rem; /* Encore plus petit pour le titre */
    }

    .explanation-eter p {
        font-size: 0.8rem; /* Texte plus petit pour les paragraphes */
        line-height: 1.4; /* Rduit l'espacement des lignes */
    }
}

/* Pour les grands crans (1200px) */
@media (min-width: 1200px) {
    .explanation-eter {
        max-width: 700px; /* Augmente la largeur pour les crans larges */
        padding: 25px; /* Augmente l'espace intrieur */
    }

    .explanation-eter h4 {
        font-size: 1.8rem; /* Agrandit le titre */
    }

    .explanation-eter p {
        font-size: 1.1rem; /* Texte lgrement plus grand */
    }
}
