.menu-item-inner-ripey {
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    color: #fff; /* Couleur du texte */
    transform: rotate(45deg); /* Applique une rotation de 45 degrs */
    transition: color 0.3s ease, text-shadow 0.3s ease; /* Animation fluide pour le texte */
}

.with-text-ripey {
    color: #ffffff;
    background-color: #20283D;
  }
  
  /* Styles spcifiques aux lments avec effet */
  .special-effect {
    transition: 0.3s ease all;
  }
  
  .special-effect:before {
    transition: 0.5s all ease;
    position: absolute;
    top: 0;
    left: 50%;
    right: 50%;
    bottom: 0;
    opacity: 0;
    content: '';
    background-color: #ffffff;
    z-index: -1;
    border-radius: 2px;
  }
  
  .special-effect:hover .menu-item-inner-ripey {
    color: #20283D;
  }
  
  .special-effect:hover:before {
    left: 0;
    right: 0;
    opacity: 1;
    background-color: #ffffff;
  }
  
  /* Effet de clic */
  .with-text:active {
    transform: scale(0.9);
  }

 